
import { defineComponent } from "vue";
import axios, { AxiosResponse } from "axios";

interface Student {
	_id: string;
	name: string;
	class: string;
	cours: {
		_id: string;
		__v: number;
		month: string;
		lessons: {
			_id: string;
			day: string;
			title: string;
			link: string;
		}[];
	}[];
}

interface DataComponent {
	studentsName: string[];
	student: string;
	month: string;
	classe: string;
	day: string;
	link: string;
	title: string;
	studentSelected: string;
	studentSelectedDeleted: string;
}

export default defineComponent({
	name: "Admin",
	data(): DataComponent {
		return {
			studentsName: [],
			student: "",
			month: "",
			classe: "",
			day: "",
			link: "",
			title: "",
			studentSelected: "",
			studentSelectedDeleted: "",
		};
	},
	methods: {
		loadStudentsName(): void {
			this.studentsName = [];
			axios
				.get("https://sheltered-basin-99154.herokuapp.com/api/student/")
				.then(
					(
						response: AxiosResponse<{ message: string; students: Student[] }>
					) => {
						response.data.students.forEach((student: Student) => {
							this.studentsName?.push(student.name);
						});
						this.student = "";
						this.classe = "";
					}
				);
		},
		submitCreateStudent(e: Event): void {
			e.preventDefault();
			if (localStorage.getItem("token") === null) {
				alert("Vous n'êtes pas connecté");
				setTimeout(() => {
					window.location.href = "/login";
				}, 5000);
				return;
			} else if (localStorage.getItem("expires_at") != null) {
				if (
					new Date(localStorage.getItem("expires_at") as string) < new Date()
				) {
					alert("Votre session a expiré");
					setTimeout(() => {
						window.location.href = "/login";
					}, 5000);
					return;
				} else {
					axios
						.post(
							"https://sheltered-basin-99154.herokuapp.com/api/student",
							{ name: this.student, class: this.classe },
							{
								headers: {
									Authorization: `Bearer ${localStorage.getItem("token")}`,
								},
							}
						)
						.then((response: AxiosResponse) => {
							console.log(response);
							alert("Ajouté !");
							this.loadStudentsName();
						})
						.catch((error: Error) => {
							alert("Erreur !");
							console.log(error);
						});
				}
			} else {
				alert("Erreur !");
			}
		},
		submitUpdateStudent(e: Event): void {
			e.preventDefault();
			axios
				.put(
					`https://sheltered-basin-99154.herokuapp.com/api/student/${this.student}`,
					{
						name: document.querySelector("select")?.value,
						month: this.month,
						lessons: [
							{
								day: this.day,
								title: this.title,
								link: this.link,
							},
						],
					},
					{
						headers: {
							Authorization: `Bearer ${localStorage.getItem("token")}`,
						},
					}
				)
				.then((response: AxiosResponse) => {
					console.log(response);
					alert("Modifié !");
					this.month = "";
					this.day = "";
					this.title = "";
					this.link = "";
				})
				.catch((error: Error) => {
					console.log(error);
				});
		},
		submitDeleteStudent(e: Event): void {
			e.preventDefault();
			axios
				.delete(`https://sheltered-basin-99154.herokuapp.com/api/student/`, {
					data: {
						name: this.studentSelectedDeleted,
					},
					headers: {
						Authorization: `Bearer ${localStorage.getItem("token")}`,
					},
				})
				.then((response: AxiosResponse) => {
					console.log(response);
					alert("Supprimé !");
					this.loadStudentsName();
				})
				.catch((error: Error) => {
					alert("Erreur !");
					console.log(error);
				});
		},
	},
	mounted() {
		this.loadStudentsName();
	},
});
